/**
 * App component
 */

.app {
  --thickness: 30px;

  margin-top: -12rem;

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  overflow: visible;
  perspective: 1500px;

  &[data-current-media="book"] {
      .book-wrapper {
          display: inline-block;
      }
      .media__button[value="book"] {
          color: black;
      }
  }
}


/**
* Wrapper component
*/

.book-wrapper {
  &:hover .book {
      transform: rotateX(5deg) rotateY(-50deg);
  }

  &:hover .book-shadow {
      transform: rotateX(90deg) rotateZ(40deg);
  }

  &:hover .movie {
      transform: rotateX(5deg) rotateY(-50deg);
  }

  &:hover .movie-shadow {
      transform: rotateX(90deg) rotateZ(40deg);
  }
}


/**
* Wrapper component
*/

.book-wrapper {
  display: none;
}
.movie-wrapper {
  display: none;
}


/**
* Book component
*/

.book {
  position: relative;
  z-index: 1;
  width: 16rem;
  transform-style: preserve-3d;
  transition: transform 1s ease;
  transform: rotateX(0deg) rotateY(-30deg);


  &__front {
    border-radius: 5px;
    overflow: hidden;
    transform: translateZ(calc(var(--thickness)));

    img {
      transform:scale(105%);
    }
  }

  &__back {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      background: #bbbbbb;
      border-radius: 5px;
      overflow: hidden;
      transform: translateZ(calc(-1 * var(--thickness)));
  }

  &__paper {
      height: 98%;
      width: calc(var(--thickness) * 2);
      background: white;
      position: absolute;
      top: 1%;
      right: 0;
      background:
          linear-gradient(90deg, rgba(0, 0, 0, 0.3), rgba(255, 255, 257, 0)),
          repeating-linear-gradient(90deg, white, #e2e2e2 1px, white 3px, #9a9a9a 1px);
      transform: rotateY(90deg) translateX(calc(var(--thickness) / 7)) translateZ(calc(var(--thickness) / 1.2));
  }
}

.book-shadow {
  position: absolute;
  width: 400px;
  height: calc(100px + var(--thickness));
  background: radial-gradient(70% 85%, rgba(0, 0, 0, 0.25) 0%, rgba(0, 0, 0, 0) 100%);
  transform: rotateX(90deg) rotateZ(30deg);
  transition: transform 1s ease;
  filter: blur(32px);
}

